<template>
<div>
  <v-card flat>
    <v-card-text>{{subsection}}</v-card-text>
  </v-card>
</div>
</template>

<script>
  export default {
    computed: {
      subsection: function(){
        var component = this;
        return component.$store.state.profile.sections.find(function(item){
          return item.id == component.$route.params.sectionID;
        }).subsections.find(function(item){
          return item.id == component.$route.params.subsectionID;
        });

      }
    }
  }
</script>